import { useEffect, useState } from 'react';
import 'react-animated-slider/build/horizontal.css';
import Slider from 'react-animated-slider';

import { AUTOPLAY, URL_FOR_SLIDER } from '../../constants';
import { ContactPage } from '../contact-page';
import { Slide } from '@/models';
import './main.css';

export const Main = () => {
  const [images, setImages] = useState([]);

  useEffect(() => {
    (async function getData() {
      const blogsDatabase = await fetch(URL_FOR_SLIDER)
      .then(res => res.json());
  
      setImages(blogsDatabase);
    })();
    
  }, []);

  return (
    <main>
      <div className="main">
        <section className="main__content">
          <Slider className="main__slider-wrapper" autoplay={AUTOPLAY} touchDisabled={false} minSwipeOffset={20}>
            {images.map(({ name, description, image }: Slide, index) => (
              <div className="main__slider-content slide" key={`${name}-${index}`} style={{ background: `center/100% url('${(image[0]).url}') no-repeat`}}>
                <div className="main__slider-inner">
                  <p>{description}</p>
                </div>
              </div>
           ))}
          </Slider>
        </section>
        <section className="main__advertising">
          <aside>Google Advertising</aside>
        </section>
        <section className="main__form">
          <ContactPage />
        </section>
      </div>
    </main>
  );
};