import { RefObject, useRef, useState } from 'react';
import ReactGA from 'react-ga';

import { UploadImageResponseType } from '@/models';
import { URL_FOR_CONTACT_PAGE, KEY_FOR_UPLOAD_IMAGE } from '../../constants';
import './contact-page.css';

function uploadImage(image: any): Promise<UploadImageResponseType> {
  const URL = `https://api.imgbb.com/1/upload?key=${KEY_FOR_UPLOAD_IMAGE}`;

  const data = new FormData();
  data.append('image', image);

  return fetch(URL, {
    method: 'POST',
    body: data,
  })
    .then((response) => {
      if (response && response.ok) {
        return response.json();
      }
    })
}

function sendDataToExcel(data: any) {
  return fetch(URL_FOR_CONTACT_PAGE, {
    method: 'POST',
    body: data,
  })
}

export const ContactPage = () => {
  const [sending, setSending] = useState(false);
  const formRef: RefObject<any> = useRef();

  const GAevent = () => {
    ReactGA.event({
      category: 'Contact Us',
      action: 'User pressed Contact Us button'
    });
  }

  const onSubmit = async (event: any) => {
    event.preventDefault();
    setSending(true);

    const data = new FormData(event.target);
    const image = data.get('file');
    data.delete('file');

    let imageUrl: string;
    let viewImageUrl: string;
    let deleteImageUrl: string;

    if (image) {
      const uploadedImage = await uploadImage(image);
      
      const responseData = uploadedImage && uploadedImage.data; 
      
      imageUrl = responseData && responseData.url;
      viewImageUrl = responseData && responseData.url_viewer;
      deleteImageUrl = responseData && responseData.delete_url;

      data.append('imageUrl', imageUrl);
      data.append('viewImageUrl', viewImageUrl);
      data.append('deleteImageUrl', deleteImageUrl);
    }

    const result = await sendDataToExcel(data);
    alert('Thank you! We\'ll connect with you soon.');
    setSending(false);
    console.log('sucsess', result);
  }

  return (
    <div className="form">
      <div className="drop" style={{visibility: sending ? 'visible' : 'hidden'}}></div>
      <div className="form__wrapper">
        <h1 className="form__title">Please contuct with us entered this form</h1>
        <form ref={formRef} onSubmit={onSubmit}>
          <div className="form__contact-data">
            <div>
              <label className="form__label">
                What is your name?
                <input type="text" name="name" />
              </label>
            </div>
            <div>
              <label className="form__label">
                How to contact with you?
                <input type="text" name="contact" />
              </label>
            </div>
          </div>
          <div>
            <label className="form__label">
              Describe your wish
              <textarea name="description" rows={20} />
            </label>
          </div>
          <div>
            <label className="form__label form__label-file">
              Attach photo
              <input type="file" name="file"/>
            </label>
          </div>
          <div className="form__button">
            <button type="submit" disabled={sending} onClick={GAevent}>Contact Us</button>
          </div>
        </form>
      </div>
    </div>
  )
}